@import "./shared.scss";
@font-face {
    font-family: 'din';
    src: local('din'), url(../fonts/D-DINExp.ttf) format('truetype');
}
body {
    margin: 0;
    font-family: din, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 14px;
    background: #070e19;
    color: #fff;
}

input,
button,
textarea {
    &:focus {
        outline: 0;
    }
}

ul {
    list-style: none;
}

body, html, #root{
    height: 100%;
}


#root {
    height: 100%;
    width: 100%;
    max-width: 750px;
    margin: 0 auto;
    position: absolute;
    top: 0;
    //opacity: 0.2;
    //background: red;
    left: 50%;
    transform: translateX(-50%);
}

#canvas-wrapper{
    height: 100%;
    width: 100%;
    margin: 0 auto;
    overflow: hidden;
}

#canvas {
    width: 100%;
    min-width: 60vh;
    height: 100vh;
    background: #fff;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
}

.clearFloat{
    zoom:1;

    &:after{
        display: block;
        clear: both;
        content: " ";
        visibility: hidden;
        height: 0;/*其他适用*/
    }
}

.wrapper{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    .main{
        flex-grow: 1;
        overflow-y: auto;
    }

}

.c-btn{
    width: px(372);
    height: px(90);
    font-size: px(50);
    color: #fff;
    text-align: center;
    //line-height: px(90);
    padding: 0 px(50);
    box-sizing: border-box;
    background-color: #4d2b8e;
    background-image: $linear-gradient;
    border: 0;
    border-radius: px(18);
}
